@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');
/* @import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;700&display=swap'); */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 16px;
}
.loading-big {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading{
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.home {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;

}
.profile-card {
    display: flex;
    flex-direction: column;
    border: double;
    border-color: rgba(0,0,0,.2);
    border-radius: .25rem;
    text-align: center;
    margin: 0em;
    justify-content: flex-start;
    margin: 1rem;

}
.info {
  display: inline-block;
  justify-content: start;
  align-items: start;
  text-align: start;
  padding: 1rem 0;
}
.info h2 {
  font-size:24px;
  font-weight: 700;
  text-align: center;
}

.info h3 {
  background-color: white;
  padding: 0.25rem 1rem;
  word-spacing: 30px;
}
.info .fal{
  margin-right: 20px;
}

.footer {
  background-color: #FAFAFA;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-evenly;
  padding: 2rem;
}

.footer hr {
  border-style: none none none solid;
  border-left: 1px solid gray;
  height: 100%
}


.fa-heart {
  color: #FC0C1B;
}

.footer > i:hover {
  transform: scale(1.5)
}

.footer {
  padding: 1rem;
}

.avataar {
  display: flex;
  position: relative;

}


.modal-fixed {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  overflow: hidden;
  z-index: 3;
}

.modal-flex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  height: auto;
  margin: 20% auto;
  background-color: white;
  border-radius: 1rem;
  overflow: hidden;
  padding: 0rem;
}

.modal-title {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.modal-title > * {
  align-self: center;
}
.fa-times:hover{
  transform: scale(2);
}

.modal-body {
  width: 100%;
  display: flex;
  flex-flow: column wrap;
  background: white;
  align-items: stretch;
  justify-content: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,.1));
  
}
.modal-body > div {
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  align-content: center;
  margin: .5rem 0;
  justify-content: space-between;
  text-align: center;
}

.modal-body input{
  width:80%;
  margin-left: 1rem;
  padding: .5rem .25rem;
  border-radius: .5rem;
  border: groove;
  flex-basis: 80%;
}
.modal-body label[required]:before{
  color: red;
  content: "*";
}

.modal-footer {
  display: inline-flex;
  justify-content: flex-end;
  padding:1rem;
}
.modal-footer > * {
  margin: 0 1rem;
}

.modal-footer > input {
  padding: .5rem;
  border-radius: .5rem;
}
.modal-footer > input:hover {
  transform: scale(1.2);
}
.ok-button {
  color: white;
  background-color: #2693FB;
}
